.Navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    animation: animater-100 forwards 2s;
    transform: translateY(-100px);
}

@keyframes animater-100 {
    100% {
        transform: translateY(0);
    }
}

.Navbar a {
    text-decoration: none;
    font-weight: 400;
    font-size: 1.5rem;
    color: teal;
    transition: 0.2s ease;
    border: 0.1rem black solid;
    padding: 0 0.5rem;
}
.Navbar a.active {
    color: white;
    border: 0.1rem white solid;
    padding: 0 0.5rem;
}
.Navbar a i {
    font-size: 1.8rem;
}

.Navbar a:hover {
    color: tomato;
    border: 0.1rem tomato solid;
}

.Navbar ul {
    display: flex;
    list-style: none;
    justify-content: space-around;
    font-weight: 700;
    background-color: black;
    color: #f3f3f3;
    height: 2.5rem;
    font-size: 1.2rem;
    align-items: center;
}

@media (max-width: 500px) {
    .Navbar {
        top: -3rem;
    }
}
