@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&family=Langar&display=swap);
body {
    width: 100%;
}

.App {
    text-align: center;
    font-family: "Roboto", sans-serif;
    width: 100%;
    font-weight: 400;
}
/* 400 , 400 i , 700 , 900 i */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.Navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    -webkit-animation: animater-100 forwards 2s;
            animation: animater-100 forwards 2s;
    transform: translateY(-100px);
}

@-webkit-keyframes animater-100 {
    100% {
        transform: translateY(0);
    }
}

@keyframes animater-100 {
    100% {
        transform: translateY(0);
    }
}

.Navbar a {
    text-decoration: none;
    font-weight: 400;
    font-size: 1.5rem;
    color: teal;
    transition: 0.2s ease;
    border: 0.1rem black solid;
    padding: 0 0.5rem;
}
.Navbar a.active {
    color: white;
    border: 0.1rem white solid;
    padding: 0 0.5rem;
}
.Navbar a i {
    font-size: 1.8rem;
}

.Navbar a:hover {
    color: tomato;
    border: 0.1rem tomato solid;
}

.Navbar ul {
    display: flex;
    list-style: none;
    justify-content: space-around;
    font-weight: 700;
    background-color: black;
    color: #f3f3f3;
    height: 2.5rem;
    font-size: 1.2rem;
    align-items: center;
}

@media (max-width: 500px) {
    .Navbar {
        top: -3rem;
    }
}

.Content {
    position: relative;
    width: 100%;
    overflow: hidden;
    z-index: 0;
}

.About {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 400px;
    background-color: black;
    color: #f3f3f3;
}

.About h1 {
    margin-bottom: 1rem;
    transition: all 0.2s ease-in;
}

.About h1:hover {
    color: rgb(214, 143, 255);
}
.About p {
    padding: 0.2rem 1rem;
    transition: all 0.2s ease-in-out;
}

.About p:hover {
    color: rgb(255, 252, 77);
}

:root {
    --primary-color: rgb(107, 0, 0);
    --secondary-color: rgb(13, 78, 0);
    --light-color: #f3f3f3;
    --dark-color: #333;
}

.Full {
    height: 100vh;
}

.op-1 {
    display: flex;
    flex-direction: column;
    background: url(/static/media/Stars.e612d8da.jpg) center
        center/cover;
    transition: 0.2s ease-in;
}

.op-1 .text-div {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    height: 40vh;
}

.op-1 .buttons-div {
    margin: 2rem 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height: 60vh;
}

.op-1 h1,
.op-1 h2 {
    color: #f3f3f3;
    color: var(--light-color);
}

.op-1 .buttons-div {
    display: flex;
    flex-direction: column;
}

.op-1 .buttons-div button {
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    font-size: 1rem;
    border: 0.2rem black solid;
    background-color: rgb(107, 0, 0);
    background-color: var(--primary-color);
    color: #f3f3f3;
    color: var(--light-color);
    transition: 0.2s ease-in;
    font-weight: 900;
}

.op-1 .buttons-div button:hover {
    font-weight: 700;
    cursor: pointer;
    border: 0.2rem rgb(13, 78, 0) solid;
    border: 0.2rem var(--secondary-color) solid;
    color: #f3f3f3;
    color: var(--light-color);
    background-color: rgb(13, 78, 0);
    background-color: var(--secondary-color);
}

/* View-2 */
.op-2 {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background: url(/static/media/Stars2.0bec1e6a.jpg) center
        center/cover;
    color: #f3f3f3;
    color: var(--light-color);
    transition: 0.2s ease-in;
}
.op-2 .text-div {
    width: 50%;
}
.op-2 .buttons-div {
    width: 50%;
}
.op-2 h2 {
    transition: 0.1s ease-in-out;
}
.op-2 h2:hover {
    color: yellowgreen;
}
.op-2 .buttons-div button {
    margin: 0.5rem;
    padding: 0.5rem;
    background-color: black;
    color: #f3f3f3;
    color: var(--light-color);
    border: 0.2rem black solid;
    transition: 0.2s ease-in;
    font-weight: 900;
}
.op-2 .buttons-div button:hover {
    background-color: rgb(107, 0, 0);
    background-color: var(--primary-color);
    font-weight: 700;
    border: 0.2rem rgb(107, 0, 0) solid;
    border: 0.2rem var(--primary-color) solid;
}

.Skills {
    height: 600px;
    display: flex;
    flex-direction: column;
    background-color: black;
    justify-content: center;
    color: #f3f3f3;
}

.Skills li {
    transition: all 0.2s ease-in-out;

    margin: 0.2rem;
}

.Skills h1 {
    transition: all 0.2s ease-in-out;

    margin: 2rem;
}

.Skills h2 {
    transition: all 0.2s ease-in-out;

    margin: 1rem;
}

.Skills li:hover {
    color: rgb(200, 255, 111);
}

.Skills h1:hover {
    color: rgb(131, 255, 214);
}

.Skills h2:hover {
    color: rgb(255, 206, 116);
}

p {
    transition: all 0.2s ease-in-out;
}

.Footer .footer-2 {
    background-color: black;
    color: white;
    height: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 0.8rem;
    transition: all 0.5s ease-in;
}

.footer-2 .heart {
    color: tomato;
    transition: all 0.2s ease-in;
}
.footer-2 .heart:hover {
    font-size: 130%;
}

.footer-2 .niks {
    transition: all 0.2s ease-in;
}

.footer-2 .niks:hover {
    color: tomato;
    font-size: 120%;
}

.Footer .footer-2:hover {
    color: rgb(255, 91, 62);
}

.upper {
    position: fixed;
    top: 0;
    left: 0;
    background-color: black;
    z-index: 2;
    height: 3rem;
    width: 100%;
    color: #f3f3f3;
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
    padding: 0.15rem 1rem;
    padding-right: 3rem;
}

.upper a {
    font-size: 2rem;
    color: #f3f3f3;
}

.upper .Ham {
    width: 15%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    transition: all 0.2s ease-in;
}

.upper .Ham:hover {
    cursor: pointer;
    height: 110%;
    width: 17%;
}

.upper .Margin {
    width: 80%;
    border-bottom: 0.4rem #f3f3f3 solid;
}

@media (min-width: 500px) {
    .SideDrawer {
        display: none;
    }
}

.SideDrawer {
    z-index: 0;
}
.ic {
    transition: all 0.3s ease-in;
}
.ic:hover {
    font-size: 120%;
    color: red;
}

.lower ul {
    margin-top: 0.5rem;
    font-size: 2.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: black;
    opacity: 92%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    width: 60%;
    z-index: 1;
    color: rgb(255, 71, 71);
    transition: transform 0.3s ease-in;
}

.lower li {
    list-style: none;
    padding: 2rem;
    transition: all 0.3s ease-in;
}
.lower li:hover {
    cursor: pointer;
    font-size: 90%;
}

.open {
    transform: translateX(0);
}

.close {
    transform: translateX(-100%);
}

