.Footer .footer-2 {
    background-color: black;
    color: white;
    height: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 0.8rem;
    transition: all 0.5s ease-in;
}

.footer-2 .heart {
    color: tomato;
    transition: all 0.2s ease-in;
}
.footer-2 .heart:hover {
    font-size: 130%;
}

.footer-2 .niks {
    transition: all 0.2s ease-in;
}

.footer-2 .niks:hover {
    color: tomato;
    font-size: 120%;
}

.Footer .footer-2:hover {
    color: rgb(255, 91, 62);
}
