.upper {
    position: fixed;
    top: 0;
    left: 0;
    background-color: black;
    z-index: 2;
    height: 3rem;
    width: 100%;
    color: #f3f3f3;
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
    padding: 0.15rem 1rem;
    padding-right: 3rem;
}

.upper a {
    font-size: 2rem;
    color: #f3f3f3;
}

.upper .Ham {
    width: 15%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    transition: all 0.2s ease-in;
}

.upper .Ham:hover {
    cursor: pointer;
    height: 110%;
    width: 17%;
}

.upper .Margin {
    width: 80%;
    border-bottom: 0.4rem #f3f3f3 solid;
}

@media (min-width: 500px) {
    .SideDrawer {
        display: none;
    }
}

.SideDrawer {
    z-index: 0;
}
.ic {
    transition: all 0.3s ease-in;
}
.ic:hover {
    font-size: 120%;
    color: red;
}

.lower ul {
    margin-top: 0.5rem;
    font-size: 2.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: black;
    opacity: 92%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    width: 60%;
    z-index: 1;
    color: rgb(255, 71, 71);
    transition: transform 0.3s ease-in;
}

.lower li {
    list-style: none;
    padding: 2rem;
    transition: all 0.3s ease-in;
}
.lower li:hover {
    cursor: pointer;
    font-size: 90%;
}

.open {
    transform: translateX(0);
}

.close {
    transform: translateX(-100%);
}
