@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&family=Langar&display=swap");
:root {
    --primary-color: rgb(107, 0, 0);
    --secondary-color: rgb(13, 78, 0);
    --light-color: #f3f3f3;
    --dark-color: #333;
}

.Full {
    height: 100vh;
}

.op-1 {
    display: flex;
    flex-direction: column;
    background: url("../../../assets/images/Stars.jpg") center
        center/cover;
    transition: 0.2s ease-in;
}

.op-1 .text-div {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    height: 40vh;
}

.op-1 .buttons-div {
    margin: 2rem 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height: 60vh;
}

.op-1 h1,
.op-1 h2 {
    color: var(--light-color);
}

.op-1 .buttons-div {
    display: flex;
    flex-direction: column;
}

.op-1 .buttons-div button {
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    font-size: 1rem;
    border: 0.2rem black solid;
    background-color: var(--primary-color);
    color: var(--light-color);
    transition: 0.2s ease-in;
    font-weight: 900;
}

.op-1 .buttons-div button:hover {
    font-weight: 700;
    cursor: pointer;
    border: 0.2rem var(--secondary-color) solid;
    color: var(--light-color);
    background-color: var(--secondary-color);
}

/* View-2 */
.op-2 {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background: url("../../../assets/images/Stars2.jpg") center
        center/cover;
    color: var(--light-color);
    transition: 0.2s ease-in;
}
.op-2 .text-div {
    width: 50%;
}
.op-2 .buttons-div {
    width: 50%;
}
.op-2 h2 {
    transition: 0.1s ease-in-out;
}
.op-2 h2:hover {
    color: yellowgreen;
}
.op-2 .buttons-div button {
    margin: 0.5rem;
    padding: 0.5rem;
    background-color: black;
    color: var(--light-color);
    border: 0.2rem black solid;
    transition: 0.2s ease-in;
    font-weight: 900;
}
.op-2 .buttons-div button:hover {
    background-color: var(--primary-color);
    font-weight: 700;
    border: 0.2rem var(--primary-color) solid;
}
