.Skills {
    height: 600px;
    display: flex;
    flex-direction: column;
    background-color: black;
    justify-content: center;
    color: #f3f3f3;
}

.Skills li {
    transition: all 0.2s ease-in-out;

    margin: 0.2rem;
}

.Skills h1 {
    transition: all 0.2s ease-in-out;

    margin: 2rem;
}

.Skills h2 {
    transition: all 0.2s ease-in-out;

    margin: 1rem;
}

.Skills li:hover {
    color: rgb(200, 255, 111);
}

.Skills h1:hover {
    color: rgb(131, 255, 214);
}

.Skills h2:hover {
    color: rgb(255, 206, 116);
}

p {
    transition: all 0.2s ease-in-out;
}
