.About {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 400px;
    background-color: black;
    color: #f3f3f3;
}

.About h1 {
    margin-bottom: 1rem;
    transition: all 0.2s ease-in;
}

.About h1:hover {
    color: rgb(214, 143, 255);
}
.About p {
    padding: 0.2rem 1rem;
    transition: all 0.2s ease-in-out;
}

.About p:hover {
    color: rgb(255, 252, 77);
}
